import React, { useEffect, useState } from 'react';
import Layout from '../components/layout';
import { graphql } from 'gatsby';
import styled from 'styled-components';
import HorizontalAds from '../components/horizontal-ads'

const OnecompilerTemplate = ({ data, location }) => {
  const [isDisplay, setIsDisplay] = useState(true);
  const { html, frontmatter } = data.markdownRemark;

  useEffect(() => {
    setInterval(() => {
      setIsDisplay(false)
      setIsDisplay(true)
    }, 30000)
  }, []);

  let url = "https://onecompiler.com/embed";
  const params = new Proxy(new URLSearchParams(location.search), {
    get: (searchParams, prop) => searchParams.get(prop),
  });

  console.log("params.language", params.language)
  if (params.language) {
    url = url + "/" + params.language;
  } else {
    url = url + "/javascript";
  }

  if (params.code) {
    url = url + "/" + params.code;
  }


  url = url + "?hideNew=true&hideNewFileOption=true&hideTitle=true&hideStdin=true";


  return (
    <Layout title={frontmatter.title}>
      <PostWrapper>
        <PostContent dangerouslySetInnerHTML={{ __html: html }} />
        {isDisplay && <HorizontalAds />}
        <iframe
          frameBorder="0"
          height="500px"
          src={url}
          width="100%"
        ></iframe>
        {isDisplay && <HorizontalAds />}
      </PostWrapper>
    </Layout>
  );
};

export default OnecompilerTemplate;

const PostWrapper = styled.div`
  padding-top: var(--size-900);
  padding-bottom: var(--size-900);
  margin-left: auto;
  flex-direction: column;
  display: flex;
  margin-right: auto;
  word-wrap: break-word;
`;

const PostContent = styled.section`
  padding-top: var(--size-800);

  & > * + * {
    margin-top: var(--size-300);
  }

  & > p + p {
    margin-top: var(--size-700);
  }

  * + h1,
  * + h2,
  * + h3 {
    margin-top: var(--size-900);
  }

  h1 {
    font-size: var(--size-800);
  }

  h2 {
    font-size: var(--size-700);
  }

  h3 {
    font-size: var(--size-700);
  }

  b,
  strong {
    font-weight: 600;
  }

  a {
    color: inherit;
    text-decoration: underline;
    text-decoration-thickness: 0.125rem;
  }

  blockquote {
    padding-left: var(--size-400);
    border-left: 5px solid;
    font-style: italic;
  }

  code {
    font-family: 'Source Sans Pro', monospace;
    overflow-x: auto;
    white-space: pre-wrap;
  }

  pre {
    overflow-x: auto;
    white-space: pre-wrap;
    max-width: 100%;
  }
`;

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        profile_image
      }
    }
  }
`;
